import { FC, useCallback, useEffect, useState } from "react";
import { Button, Container, InputBox, ToggleBox } from "./ui";
import { EyeIcon } from "./icons";
import { useDebounce } from "../hooks";
import * as yup from "yup";
import { useFormik } from "formik";

interface FormVal {
  email: string;
  description: string;
  password: string;
  repeatPassword?: string;
  rememberMe: boolean;
}

const schema = yup.object().shape({
  email: yup
    .string()
    .email("  ")
    .required(" "),
  description: yup.string().required(" "),
  password: yup.string().required(" "),
  repeatPassword: yup
    .string()
    .oneOf([yup.ref("password")], "  ")
    .required(" "),
});

export const Form: FC = () => {
  const formik = useFormik<FormVal>({
    validationSchema: schema,
    validateOnBlur: true,
    initialValues: {
      email: "",
      description: "",
      password: "",
      repeatPassword: "",
      rememberMe: false,
    },
    onSubmit(values) {},
  });

  const [passwordHidden, setPasswordHidden] = useState(true);
  const [errors, setErrors] = useState({});

  const togglePasswordVisibility = () => {
    setPasswordHidden((prev) => !prev);
  };

  const getError = (field: keyof FormVal) => {
    return formik.touched[field] ? formik.errors[field] : "";
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    formik.handleChange(e);
  };
  return (
    <Container className="centered">
      <form onSubmit={formik.handleSubmit} noValidate>
        <InputBox
          placeholder=". "
          type="email"
          error={getError("email")}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          value={formik.values.email}
          name="email"
        />
        <InputBox
          placeholder=""
          error={getError("description")}
          autoGrow
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          value={formik.values.description}
          name="description"
        />
        <InputBox
          type={passwordHidden ? "password" : "text"}
          placeholder=""
          error={getError("password")}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          value={formik.values.password}
          name="password"
          icon={
            <EyeIcon
              onClick={togglePasswordVisibility}
              className="EyeIcon"
              hidden={passwordHidden}
            />
          }
        />
        <InputBox
          type={passwordHidden ? "password" : "text"}
          placeholder=" "
          error={getError("repeatPassword")}
          onChange={formik.handleChange}
          onBlur={formik.handleBlur}
          value={formik.values.repeatPassword}
          name="repeatPassword"
        />
        <ToggleBox
          className="push"
          label=" "
          onChange={(value) => formik.setFieldValue("rememberMe", value)}
          active={formik.values.rememberMe}
        />
        <Button type="submit"></Button>
      </form>
    </Container>
  );
};
